// Import Partials
// ================================
	@import '_partials/base';



// Custom css
// ================================ 

.error{
	text-align: center;
	padding: 0 10px;
	position: relative;
	max-width: 1477px;
	margin: 0 auto;
	width: 100%;
	height: 100%;
	//padding-top: 53%;
	background: url('pic/404.jpg') center center no-repeat;
	background-size: cover;

	&__head{
		position: absolute;
		top: 7%;
	    left: 10px;
	    right: 10px;
	}
	&__content{
		position: absolute;
		top: 70%;
		left: 10px;
		right: 10px;
	}
	&__text{
		font-size: 14px;
		line-height: 30px;
		color: #343434;
		font-weight: $f_light;
		a{
			text-decoration: underline;
			color: #e33;
			&:hover{
				text-decoration: none;
			}
		}
	}
	&__title{
		font-size: rem(32);
	    color: #212121;
	    font-weight: $f_thin;
	    text-transform: uppercase;
	    letter-spacing: rem(8);
	    margin: 0;
	}
}