////
/// @group _partials/base.scss
////

// scss
// ================================

html {
	position: relative;
	height: 100%;
	font-size: 12px;
	max-width: 2580px;
	width: 100%;
	margin: 0 auto;
	@include media( $md ) {
		font-size: 14px;
	}
	@include media( $lg ) {
		font-size: 16px;
	}
}

*, :after, :before {
	box-sizing: inherit;
}

a {
	color: #43f;
}

body {
	position: relative;
	height: 100%;
	box-sizing: border-box;
	min-width: 320px;
}

pre {
	padding: 1.2em;
	tab-size: 4;
	border-width: 0;
	white-space: pre;
	white-space: pre-wrap;
	background: #eee;
	word-wrap: break-word;
}

img {
	max-width: 100%;
	//height: auto;
}

small {
	display: inline-block;
    vertical-align: text-bottom;
}

svg {
	pointer-events: none;
}

hr {
	border-width: 0;
	border-top: 1px solid #aaa;
}

::selection {
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

::-moz-selection {
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

::-ms-selection{
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

input {
	box-shadow: none;
	@include placeholder( #999, true );
	@include placeholder-on-focus();
}

textarea {
	box-shadow: none;
	@include placeholder( #999, true );
	@include placeholder-on-focus();
}


ul,ol{
	padding: 0;
	margin: 0;
	list-style: none;
}
a{
	text-decoration: none;
	color: $c_lnk1;
	&:hover{
		color: $c_hv;
		text-decoration: underline;
	}
}
h1,h2,h3,h4,h5,h6{
	margin: 0;
}
a,button,textarea,input{
	transition: all 0.3s;
}
input,textarea,select{
	font-family: $fnt_main;
}
a:focus,
button:focus,
textarea:focus,
input:focus,
select:focus{
	outline: none;
}
svg{
	transition: all 0.3s;
}


.order--md-1{
	@include media($md, max){
		order: -1;
	}
}




//IE
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	.partners__cnt{
		height: 518px;
	}
	.invest__wrap{
		height: 600px;
	}
}


